import React from "react";
import { Link } from "react-router-dom";

function SignupForm() {
  return (
    <React.Fragment>
      <div className="uk-section uk-section-default">
        <div className="uk-container uk-container-small">
          <div id="login" className="uk-width-large uk-margin-auto">
            <form
              action="#"
              method="post"
              className="uk-form-stacked"
            >
              <h1 className="uk-h2 uk-margin-medium-bottom">Signup with Dehnovo</h1>
              <div
                className="uk-text-lead uk-text-danger"
                style={{display: "none"}}
              ></div>
              <div className="uk-margin">
                <label for="user" className="uk-form-label">
                  Name
                </label>
                <input
                  id="user"
                  type="text"
                  name="name"
                  className="uk-input"
                  placeholder="Enter Name"
                />
              </div>
              <div className="uk-margin">
                <label for="user" className="uk-form-label">
                  Email
                </label>
                <input
                  id="user"
                  type="text"
                  name="username"
                  className="uk-input"
                  placeholder="Enter Email"
                />
              </div>
              <div className="uk-margin">
                <div className="uk-form-label uk-flex uk-flex-between">
                  <div className="uk-flex uk-flex-between">
                    <label for="password" className="uk-">
                      Password
                    </label>
                  </div>
                </div>
                <input
                  id="password"
                  type="password"
                  name="password"
                  autocomplete="current-password"
                  className="uk-input"
                  placeholder="Type Password"
                />
              </div>
              <div className="uk-margin">
                <label for="user" className="uk-form-label">
                  Mobile
                </label>
                <input
                  id="user"
                  type="text"
                  name="mobile"
                  className="uk-input"
                  placeholder="Enter Mobile"
                />
              </div>
              
              <div className="uk-margin-medium-top uk-text-center">
                <button
                  type="submit"
                  className="uk-button uk-button-primary"
                >
                  Sign in
                </button>
              </div>
              <p className="uk-text-center">
                I have an account?
                <Link to="/login"> Sign In</Link>
              </p>
            </form>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default SignupForm;
