import React from "react";
import { Link } from "react-router-dom";

function ProductCard({image, title, description}) {

    // const {title, image, description} = this.props;
  return (
    <React.Fragment>
        <div>
          <Link to="/requirements/productId" style={{textDecoration: "none"}}><div className="uk-card uk-card-default">
            <div className="uk-card-media-top">
              <img
                src={image}
                alt=""
              />
            </div>
            <div className="uk-card-body">
              <h6 className="uk-margin-remove uk-text-center" style={{fontWeight: 500}}>{title}</h6>
              <p  className="uk-text-center">
                <Link to="/chat/efhewrfj" className="uk-link-heading"> <span  uk-icon="phone"> </span> Chat with client</Link>
                </p>
                <p className="uk-text-center">
                <button className="uk-button uk-button-primary">View Detail</button>
                </p>
            </div>
          </div></Link>
        </div>
    </React.Fragment>
  );
}

export default ProductCard;
