import React from "react";
import Layout from "../layouts/Layout";
import { Link } from "react-router-dom";

function ProductDetails() {
  return (
    <Layout>
      <div className="uk-container uk-section">
        <div className="uk-child-width-1-2@s" uk-grid="true">
          <div>
            <img
              src="https://images-na.ssl-images-amazon.com/images/I/61EDIGF7kmL._SL1500_.jpg"
              alt="Product Image"
            />
          </div>
          <div>
            <h1 className="uk-text-bold">100 Laptops Needed</h1>
            <p>Compnay Name - Bangalore</p>
            <div className="uk-margin">
            <Link to="/" className="uk-link-heading uk-text-bold">
              <span uk-icon="comment"></span> Chat With Client
            </Link>
            </div>
            <div className="uk-text-muted">
              <p>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Soluta,
                unde perferendis mollitia tempore necessitatibus odio earum
                tenetur dolor quas illo consequuntur a illum magnam minima
                distinctio natus asperiores! Nisi iure, exercitationem
                accusantium unde perspiciatis itaque magni eveniet dolore eum
                quo suscipit labore nihil perferendis, qui eos iste aliquam
                officia impedit hic quod, quaerat voluptatem repellat sint.
                Pariatur aliquam distinctio maiores.
              </p>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default ProductDetails;
