import React, { Fragment } from "react";
import { Link, withRouter } from "react-router-dom";
import logo from "./logo.png";
import { Navbar, Nav, NavDropdown } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";

function Layout({ children }) {
  const nav = () => (
    <React.Fragment>
      <Navbar
        bg="light"
        expand="lg"
        className="uk-hidden@m"
        uk-sticky="sel-target: .uk-navbar-container; cls-active: uk-navbar-sticky; bottom: #transparent-sticky-navbar"
        style={{maxHeight: "70vh", overflow: "auto"}}
      >
        <Navbar.Brand>
          <Link to="/">
            <img src={logo} alt="" style={{ maxHeight: 60 }} />
          </Link>
        </Navbar.Brand>
        <Navbar>
          <Link to="/post">
            <button className="uk-button uk-button-primary uk-border-rounded uk-button-small">
              Post Requirement
            </button>
          </Link>
        </Navbar>
        <Navbar.Toggle aria-controls="basic-navbar-nav" style={{border: "none"}} />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link >
              <Link to="/"style={{textDecoration: "none", color: "black"}}>
                <span uk-icon="home" class="uk-margin-small-right"></span> Home
              </Link>
            </Nav.Link>
            <Nav.Link >
              <Link to="/requirement-list"style={{textDecoration: "none", color: "black"}}>
                <span uk-icon="file-text" class="uk-margin-small-right"></span>{" "}
                Requirements
              </Link>
            </Nav.Link>
            <Nav.Link >
              <Link to="/about-us"style={{textDecoration: "none", color: "black"}}>
                <span uk-icon="user" class="uk-margin-small-right"></span> About
                Us
              </Link>
            </Nav.Link>
            <Nav.Link >
              <Link to="/contact-us"style={{textDecoration: "none", color: "black"}}>
                <span uk-icon="comment" class="uk-margin-small-right"></span>{" "}
                Contact Us
              </Link>
            </Nav.Link>
            <NavDropdown title="Account" id="basic-nav-dropdown" style={{border: "none", backgroundColor: "transparent"}}>
              {/* <NavDropdown.Item>Account</NavDropdown.Item> */}
              <NavDropdown.Item style={{border: "none", backgroundColor: "transparent"}}>
                <Link to="#" style={{textDecoration: "none", color: "black"}} >
                  {" "}
                  <span
                    uk-icon="user"
                    class="uk-margin-small-right"
                  ></span>{" "}
                  Account
                </Link>
              </NavDropdown.Item>
              <NavDropdown.Item>
                <Link to="/requirement-list" style={{textDecoration: "none", color: "black"}}>
                  {" "}
                  <span
                    uk-icon="comment"
                    class="uk-margin-small-right"
                  ></span>{" "}
                  Your Posts for vendor
                </Link>
              </NavDropdown.Item>
              <NavDropdown.Item>
                <Link to="#" style={{textDecoration: "none", color: "black"}} >
                  {" "}
                  <span
                    uk-icon="comment"
                    class="uk-margin-small-right"
                  ></span>{" "}
                  Applied Post for Client
                </Link>
              </NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item>
                <Link to="/"  style={{textDecoration: "none", color: "black"}} >
                  {" "}
                  <span
                    uk-icon="sign-out"
                    class="uk-margin-small-right"
                  ></span>{" "}
                  Logout
                </Link>
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      <div
        uk-sticky="sel-target: .uk-navbar-container; cls-active: uk-navbar-sticky; bottom: #transparent-sticky-navbar"
        className="uk-visible@m"
      >
        <nav
          className="uk-navbar-container"
          uk-navbar="true"
          style={{ position: "relative", zIndex: 980 }}
        >
          <div className="uk-navbar-left">
            <ul className="uk-navbar-nav">
              <li>
                <Link to="/">
                  <img src={logo} alt="" style={{ maxHeight: 60 }} />
                </Link>
              </li>
            </ul>
          </div>

          <div className="uk-navbar-right">
            <ul className="uk-navbar-nav">
              <li className="uk-active">
                <Link to="/">Home</Link>
              </li>
              <li>
                <Link to="/requirement-list">Requirements</Link>
              </li>
              <li>
                <Link to="/about-us">About Us</Link>
              </li>
              <li>
                <Link to="/contact-us">Contact Us</Link>
              </li>
              {/* <li>
              <Link to="/login">Login</Link>
            </li>
            <li>
              <Link to="/signup">Sign Up</Link>
            </li> */}

              <li>
                <Link to="#">Account</Link>
                <div className="uk-navbar-dropdown">
                  <ul className="uk-nav uk-navbar-dropdown-nav">
                    <li className="uk-active">
                      <Link to="/">Account</Link>
                    </li>
                    <li>
                      <Link to="/requirement-list">Your Posts for vendor</Link>
                    </li>
                    <li>
                      <Link to="/chat">Applied Post for Client</Link>
                    </li>
                    <li>
                      <Link to="/">Logout</Link>
                    </li>
                  </ul>
                </div>
              </li>
              <li>
                <Link to="/post">
                  <button className="uk-button uk-button-primary ">
                    Post Requirements
                  </button>
                </Link>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    </React.Fragment>
  );

  const footer = () => (
    <Fragment>
      <div
        className="uk-section-default uk-padding uk-navbar-container"
        uk-scrollspy="target: [uk-scrollspy-class]; cls: uk-animation-slide-left-small; delay: false;"
      >
        <div className="uk-container">
          <div className="uk-grid-margin uk-grid" uk-grid="">
            <div className="uk-grid-item-match uk-flex-middle uk-width-expand@m uk-first-column">
              <div className="uk-panel uk-width-1-1">
                <div
                  className="uk-margin uk-text-left@m uk-text-center uk-scrollspy-inview uk-animation-slide-left-small"
                  uk-scrollspy-className=""
                >
                  <Link className="el-link" to="/">
                    <img
                      className="el-image"
                      alt="Fuse"
                      data-src={logo}
                      uk-img=""
                      src={logo}
                      style={{ maxHeight: 70 }}
                    />
                  </Link>
                </div>
              </div>
            </div>

            <div className="uk-grid-item-match uk-flex-middle uk-width-expand@m">
              <div className="uk-panel uk-width-1-1">
                <div
                  className="uk-text-center uk-scrollspy-inview uk-animation-slide-bottom-small"
                  uk-scrollspy-className="uk-animation-slide-bottom-small"
                >
                  Copyright © 2020 Dehnovo - All Rights Reserved.
                </div>
              </div>
            </div>

            <div className="uk-grid-item-match uk-flex-middle uk-width-expand@m">
              <div className="uk-panel uk-width-1-1">
                <div
                  className="uk-margin uk-text-right@m uk-text-center uk-scrollspy-inview uk-animation-slide-right-small"
                  uk-scrollspy-className="uk-animation-slide-right-small"
                >
                  {" "}
                  <div
                    className="uk-child-width-auto uk-grid-small uk-flex-right@m uk-flex-center uk-grid"
                    uk-grid=""
                  >
                    <div className="uk-first-column">
                      <a
                        className="el-link uk-link-muted uk-icon"
                        href="https://twitter.com/"
                        uk-icon="icon: twitter;"
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        <svg
                          width="20"
                          height="20"
                          viewBox="0 0 20 20"
                          xmlns="http://www.w3.org/2000/svg"
                          data-svg="twitter"
                        >
                          <path d="M19,4.74 C18.339,5.029 17.626,5.229 16.881,5.32 C17.644,4.86 18.227,4.139 18.503,3.28 C17.79,3.7 17.001,4.009 16.159,4.17 C15.485,3.45 14.526,3 13.464,3 C11.423,3 9.771,4.66 9.771,6.7 C9.771,6.99 9.804,7.269 9.868,7.539 C6.795,7.38 4.076,5.919 2.254,3.679 C1.936,4.219 1.754,4.86 1.754,5.539 C1.754,6.82 2.405,7.95 3.397,8.61 C2.79,8.589 2.22,8.429 1.723,8.149 L1.723,8.189 C1.723,9.978 2.997,11.478 4.686,11.82 C4.376,11.899 4.049,11.939 3.713,11.939 C3.475,11.939 3.245,11.919 3.018,11.88 C3.49,13.349 4.852,14.419 6.469,14.449 C5.205,15.429 3.612,16.019 1.882,16.019 C1.583,16.019 1.29,16.009 1,15.969 C2.635,17.019 4.576,17.629 6.662,17.629 C13.454,17.629 17.17,12 17.17,7.129 C17.17,6.969 17.166,6.809 17.157,6.649 C17.879,6.129 18.504,5.478 19,4.74"></path>
                        </svg>
                      </a>{" "}
                    </div>
                    <div>
                      <a
                        className="el-link uk-link-muted uk-icon"
                        href="https://www.facebook.com/"
                        uk-icon="icon: facebook;"
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        <svg
                          width="20"
                          height="20"
                          viewBox="0 0 20 20"
                          xmlns="http://www.w3.org/2000/svg"
                          data-svg="facebook"
                        >
                          <path d="M11,10h2.6l0.4-3H11V5.3c0-0.9,0.2-1.5,1.5-1.5H14V1.1c-0.3,0-1-0.1-2.1-0.1C9.6,1,8,2.4,8,5v2H5.5v3H8v8h3V10z"></path>
                        </svg>
                      </a>{" "}
                    </div>
                    <div>
                      <a
                        className="el-link uk-link-muted uk-icon"
                        href="https://plus.google.com"
                        uk-icon="icon: google-plus;"
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        <svg
                          width="20"
                          height="20"
                          viewBox="0 0 20 20"
                          xmlns="http://www.w3.org/2000/svg"
                          data-svg="google-plus"
                        >
                          <path d="M12.9,9c0,2.7-0.6,5-3.2,6.3c-3.7,1.8-8.1,0.2-9.4-3.6C-1.1,7.6,1.9,3.3,6.1,3c1.7-0.1,3.2,0.3,4.6,1.3 c0.1,0.1,0.3,0.2,0.4,0.4c-0.5,0.5-1.2,1-1.7,1.6c-1-0.8-2.1-1.1-3.5-0.9C5,5.6,4.2,6,3.6,6.7c-1.3,1.3-1.5,3.4-0.5,5 c1,1.7,2.6,2.3,4.6,1.9c1.4-0.3,2.4-1.2,2.6-2.6H6.9V9H12.9z"></path>
                          <polygon points="20,9 20,11 18,11 18,13 16,13 16,11 14,11 14,9 16,9 16,7 18,7 18,9"></polygon>
                        </svg>
                      </a>{" "}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
  return (
    <Fragment>
      {nav()}
      {children}
      {footer()}
    </Fragment>
  );
}

export default withRouter(Layout);
